<template>
  <div class="wrap fengmian">
    <h2>
      Welcome
    </h2>
    <!-- 定时器跳转 -->
  </div>
</template>

<script>
export default {
  data() {
    return {};
  },
  mounted() {
    this.gotoIndex();
  },
  components: {},
  methods: {
    gotoIndex() {
      setTimeout(() => {
        this.$router.push("/search");
      }, 3000);
    },
  },
};
</script>

<style lang="scss" scoped>
.fengmian {
  background: url("../img/fengmian.jpg");
  background-size: 100% 100%;
}
h2 {
  position: absolute;
  left: 50%;
  top: 40%;
  font-size: 40px;
  -webkit-transform: translateX(-50%);
  -ms-transform: translateX(-50%);
  -o-transform: translateX(-50%);
  transform: translateX(-50%);
  color: #fff;
  opacity: 0.1;
  animation: animated_div 3s;
  -moz-animation: animated_div 3s; /* Firefox */
  -webkit-animation: animated_div 3s; /* Safari 和 Chrome */
  -o-animation: animated_div 3s;
}

@keyframes animated_div {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
</style>
